@import "https://fonts.googleapis.com/css?family=Bungee";
@import "https://fonts.googleapis.com/css?family=Zen+Dots";
* {
  margin: 0;
  padding: 0;
}

body {
  background: linear-gradient(#141e30, #243b55);
  position: relative;
  overflow: hidden;
}

.container-figure {
  position: relative;
  overflow: hidden;
}

.text-container {
  color: #fff;
  justify-content: center;
  align-items: center;
  width: calc(100% - 50px);
  height: 50vh;
  font-size: 20px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
}

.fixation {
  color: #fff;
  text-align: "center";
  display: "inline-block";
  position: "relative";
  font-size: 50px;
  position: absolute;
}

.pause {
  color: #ff4500;
  font-size: 30px;
  position: absolute;
  transform: translate(-45%, -10%);
}

.circle {
  background-color: gray;
  border: 2px solid #000;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin: 10px;
  position: absolute;
  transform: translate(-33px, -24px);
}

.container-confidence {
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  gap: 10px;
  margin-bottom: 2vh;
  display: grid;
  position: absolute;
  bottom: 5vh;
  transform: translate(5px);
}

.btn-confidence {
  color: green;
  cursor: pointer;
  text-align: center;
  -webkit-user-select: none;
  user-select: none;
  background-color: #000;
  border: 0;
  border-radius: 4px;
  padding: 7px 20px;
  font-family: Bungee, sans-serif;
  font-size: 20px;
  text-decoration: none;
  transition: all .25s;
  display: inline-block;
  box-shadow: inset 0 -25px 18px -14px #2cbb6333, 0 1px 2px #2cbb6326, 0 2px 4px #2cbb6326, 0 4px 8px #2cbb6326, 0 8px 16px #2cbb6326, 0 16px 32px #2cbb6326;
}

.btn-confidence:active {
  background-color: #c2fbd7;
  transform: scale(1.05)rotate(-1deg);
  box-shadow: inset 0 -25px 18px -14px #2cbb6359, 0 1px 2px #2cbb6340, 0 2px 4px #2cbb6340, 0 4px 8px #2cbb6340, 0 8px 16px #2cbb6340, 0 16px 32px #2cbb6340;
}

.btn--training, .btn--next {
  color: #000;
  text-transform: uppercase;
  text-align: center;
  background-color: #b3b3b3;
  border: none;
  border-radius: 4px;
  width: 200px;
  padding: 10px;
  font-family: Bungee, sans-serif;
  font-size: 30px;
  position: relative;
}

.btn--next:active {
  color: #b3b3b3;
  background-color: #000;
}

.btn--ok {
  color: #000;
  text-transform: uppercase;
  text-align: center;
  background-color: #b3b3b3;
  border: none;
  border-radius: 4px;
  width: 200px;
  padding: 10px;
  font-family: Bungee, sans-serif;
  font-size: 30px;
  position: absolute;
}

.txt--score {
  color: #fff;
  font-family: Bungee, sans-serif;
  font-size: 20px;
  position: absolute;
  top: 40px;
  right: 15px;
}

.increase {
  text-align: center;
  color: #fff;
  width: 100%;
  max-width: 100%;
  height: 200px;
  font-family: Bungee, sans-serif;
  font-size: 50px;
  line-height: 100px;
  position: absolute;
  top: 40%;
  left: 12%;
  transform: translate(-50px, -50px) !important;
}

.circle--active {
  background-color: #fff;
  border: 2px solid green;
  width: 90px;
  height: 90px;
  transform: translate(-50px, -50px);
}

.circle--reproduction:active {
  background-color: #daa520;
  width: 90px;
  height: 90px;
  transform: translate(-50px, -50px);
}

.success {
  background: linear-gradient(to top left, #065535, #1a0a0a);
}

.fail {
  background: linear-gradient(to top left, #400000, #000);
}

.moderate--failure {
  background: linear-gradient(to top left, #8c5702, #000);
}

.chart {
  background: #ffffff1a;
  border-radius: 100px;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 5px;
  padding: 0 5px;
  display: flex;
  position: absolute;
  bottom: 20vh;
}

.chart span {
  color: #fff;
  margin-left: 5px;
  font-weight: bolder;
}

.bar {
  background: #fff;
  border-radius: 100px;
  width: 0;
  height: 30px;
  animation: 1.5s forwards load;
  box-shadow: 0 10px 40px -10px #fff;
}

:root {
  --my-start-width: 0%;
  --my-end-width: 100%;
}

@keyframes load {
  0% {
    width: var(--my-start-width);
  }

  100% {
    width: var(--my-end-width);
  }
}

.no--zoom {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  -khtml-user-select: none;
}

.hidden {
  display: none;
}
